import React from 'react';
import { RecipeFoodCard, RecipeFoodRow } from '../compontents';
import { MOCK_FOOD_DB } from '../constants/mock-food';
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';

const getFoodListItems = () => {
  const foodListItems = [];

  MOCK_FOOD_DB.map((foodType) => {
    foodType.foodList.forEach((food) =>
      foodListItems.push({ title: food.title, id: food.id })
    );
  });

  return foodListItems;
};

export const RecipeFoodPage = () => {
  const options = getFoodListItems().map((option) => {
    const firstLetter = option.title[0].toUpperCase();
    return {
      firstLetter: /[0-9]/.test(firstLetter) ? '0-9' : firstLetter,
      ...option,
    };
  });

  return (
    <div className='food-page'>
      <div className='food-page__header'>
        <Autocomplete
          id='grouped-demo'
          options={options.sort(
            (a, b) => -b.firstLetter.localeCompare(a.firstLetter)
          )}
          groupBy={(option) => option.firstLetter}
          getOptionLabel={(option) => option.title}
          style={{ width: 300 }}
          renderInput={(params) => (
            <TextField {...params} label='Vyhledat jídlo' />
          )}
        />
      </div>
      <div className='food-page__content'>
        {MOCK_FOOD_DB.map((foodType, indexRow) => {
          return (
            <RecipeFoodRow key={indexRow} title={foodType.title}>
              {foodType.foodList.map((food, indexCard) => {
                return (
                  <RecipeFoodCard
                    key={indexCard}
                    foodId={food.id}
                    title={food.title}
                    imgSrc={food.imgSrc}
                    nutrition={food.nutrition}
                  />
                );
              })}
            </RecipeFoodRow>
          );
        })}
      </div>
    </div>
  );
};
